
*{
  margin: 0;
  padding: 0;
}
*::selection{
  background-color: black;
  color: white;
}

body{
  background: linear-gradient(150deg, #3A81CE,#ffff, #081553);
  background-size: 400% 300%;
  animation: BgGradient 5s ease infinite;
}

@keyframes BgGradient {
  0% {background-position: 0% 50%;}
  50% {background-position: 100% 50%;}
  100% {background-position: 0% 50%;}  
}

#sidebar{
  position: fixed;
  height: 100%;
  width: 200px;
  background-color: rgb(20, 20, 20);
  left: -200px;
  transition: all 500ms linear;
}

#sidebar.active{
  left: 0;
}

#sidebar ul li{
  color: aliceblue;
  text-align: center;
  padding: 20px 10px;
  font-size: 20px;
  border-bottom: rgba(100,100,100,.3) solid 1px;
  list-style: none;
} 
#sidebar ul li:hover{ 
  cursor: pointer;
  background-color: rgba(60,60,60,.3);
}

.logo{
  width: 100%;
  display: block;
  margin: 0 auto;
}

#sidebar .toggle-btn{
  position: absolute;
  left: 220px;
  top: 30px;
  cursor: pointer;
}

#sidebar .toggle-btn span{
  display: block;
  width: 40px;
  text-align: center;
  font-size: 1.8em;
  color: #030303;
}

#sidebar .toggle-btn span:hover{
  font-size: 1.9em;
}

.formulario-contacto{
  height: 665px;
}

.descripcion{
  padding: 20px 100px;
}

.descripcion h1{
  text-align: center;
  font-size: 20px;
}
.descripcion p{
  text-align: justify;
  font-size: 15px;
}

a{
  text-decoration: none;
}

#contenedor-galeria{
  height: auto;
  border-top: 4px solid black;
  align-items: center;
}
#contenedor-galeria h2{
  text-align: center;
  color: #fefeff;
  text-shadow: 0px 0px 3px #081553;
  letter-spacing: 2px;
  padding-top: 5px;
}  

#contenedor-galeria div{
  display: flex;
  flex-wrap: wrap;
  padding: auto;
  margin-top: 15px;
  justify-content: space-around;
}
.foto-galeria img{
  width: 200px;
  height: auto;
  overflow: hidden;
  margin: 15px;
  cursor: pointer;
}
.foto-galeria img:hover{
  box-shadow: 0px 0px 10px white;
}
.contenedor-imagen-amplia{
  display: none;
}
.contenedor-imagen-amplia:target{
  display: block;
  position: fixed;
  background: rgba(5, 5, 5, 0.815);
  top: 0; left: 0;
  width: 100%; height: 100%;
}

.contenedor-imagen-amplia h3{
  color: #fff;
  font-size: 30px;
  text-align: center;
  margin: 15px 0;
}

.imagen-modal{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.imagen-modal a{
  color: #fff;
  font-size: 40px;
  text-decoration: none;
  margin: 0 10px;
}

.imagen-modal img{
  width: 500px;
  height: 100%;
  max-width: 100%;
  border: 6px solid #fff;
  box-sizing: border-box;
}

.cerrar{
  display: block;
  width: 25px;
  height: 25px;
  margin: auto;
  text-align: center;
  text-decoration: none;
  font-size: 25px;
}

